@use '@fx/app-front-end-components/lib/sass/abstracts/bp.module.scss';
@use 'styles/sass/variables/layout-vars.module.scss';
@use 'styles/sass/variables/vars.module.scss';

$button-height: clamp(0px, 100%, 32px);

.infoPage {
  height: 100%;
  width: 100%;
  overflow-y: hidden;
  background: var(--theme-color-background-base-1);
}

.pageContentWrapper {
  height: 100%;
  overflow-x: hidden;
}

.pageContent {
  z-index: 1;
  position: relative;
  height: fit-content;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow-y: auto;
}

.hero {
  position: relative;
  width: 100%;
  z-index: 1;
  overflow: hidden;
  height: 33vh;
  min-height: clamp(480px, 42.5vw, 1000px);

  // Different from Home Page
  min-height: max(640px, 100vh);
}

.heroCarouselWrapper {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1;

  > div {
    min-width: 100%;
    height: 100%;
  }

  .heroImage {
    width: 100%;
    object-fit: cover;
    object-position: top;
    transform: translateX(10%);
    filter: saturate(1.1) hue-rotate(0deg);

    // Different from Home Page
    min-height: 100vh;
  }
}

.heroDarkPane {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 2;
  background: rgba(0, 0, 0, 0.1);
}

.heroGradient {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 50%;
  width: 100%;
  z-index: 4;
  background: linear-gradient(0deg, rgba(20,21,23,1) 10%, rgba(20,21,23,0) 100%);

  &.left {
    height: 100%;
    width: 66%;

    // Different from Home Page
    background: linear-gradient(90deg, rgba(20,21,23,1) 35%, rgba(20,21,23,0) 100%);
  }
}

.heroContent {
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 5;
}

.heroContentPadding {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  // padding-bottom: 4.5vw;

  // Different from Home Page
  width: 40vw;
  background: var(--theme-color-background-base-1);
  // padding-right: 2.5vw;
  min-width: 420px;

  @media screen and (max-width: 1028px) {
    width: 100%;
    min-width: unset;
  }
}

.heroContentBox {
  height: fit-content;
  gap: clamp(32px, 1.5vw, 128px);
  border-radius: 1vw;
  filter: drop-shadow(4px 4px 6px rgba(0, 0, 0, 0.2));
  flex-direction: column;
  min-height: 280px;

  // Different from Home Page
  width: 100%;

  .heroTitle {
    font-size: clamp(28px, 2vw, 40px);
    // color: var(--theme-color-red-6);
  }

  .words {
    gap: 1vw;
    
    :global(.font) {
      // color: var(--theme-color-gray-5);
      color: var(--theme-color-red-4);
    }

    // flex-direction: column;
    // font-size: clamp(28px, 1.6vw, 40px);
  }

  .heroDescription {
    flex-direction: column;
    // background: var(--theme-color-dark-7);
    // padding: 1vw 1.75vw;
    // border-radius: 0.75vw;
    // border: 1px solid var(--theme-color-dark-4);
    // gap: clamp(16px, 1vw, 42px);
    // transform: translateX(-1.75vw);
    // min-width: 360px;
    font-size: clamp(20px, 1vw, 32px);
    // color: var(--theme-color-gray-3);

    // Different from Home Page
    width: 100%;

    a {
      text-decoration: underline;
      color: var(--theme-color-blue-5);
    }
  }
}

.pageBodyContent {
  flex-direction: column;
  font-size: clamp(16px, 1vw, 22px);
  padding-bottom: 5vw;
  
  :global(.font) {
    color: var(--theme-color-gray-3);
  }
}

.carouselWrapper {
  width: 100%;
  height: clamp(64px, calc(150px - 3vw), 150px);
  border-top: 1px solid var(--theme-color-gray-6);
  border-bottom: 1px solid var(--theme-color-gray-6);

  > div {
    min-width: 100%;
    height: 100%;
  }
}

.carouselItem {
  height: 100%;

  .anchorButton {
    gap: clamp(16px, 2vw, 24px);
      font-size: clamp(20px, 1.35vw, 28px) !important;

    :global(.font) {
      color: var(--theme-color-white);
    }

    svg {
      height: clamp(16px, 1.25vw, 32px);
      width: clamp(16px, 1.25vw, 32px);
      position: relative;
      top: 0.05vw;
      
      * {
        fill: var(--theme-color-gray-3);
      }
    }
  }
}

.bodyContent {
  padding-top: 5vw;
  align-items: center;
  flex-direction: column;
  gap: 2.5vw;
}

.textBlock {
  // max-width: 50%;
  
  :global(.font) {
    font-size: clamp(18px, 0.9vw, 24px);
  }
}

.quote {
  max-width: 66.6%;
  text-align: center;
  font-style: italic;

  @media only screen and (max-width: 1028px) {
    max-width: 120px;
  }
}

.socials {
  gap: clamp(20px, 1.25vw, 42px);

  svg {
    height: clamp(32px, 1.5vw, 42px);
    width: clamp(32px, 1.5vw, 42px);

    * {
      fill: var(--theme-color-white);
    }
  }
}