@use 'styles/sass/variables/layout-vars.module.scss';

.header {
  position: fixed;
  top: 0;
  left: 0;
  height: 128px;
  width: 100%;
  z-index: 5;
  background: linear-gradient(0deg, rgba(20,21,23,0) 10%, rgba(20,21,23,1) 100%);
  flex-direction: column;
  justify-content: start;
  pointer-events: none;
  touch-action: none;
}

.headerContentsWrapper {
  position: relative;
  height: layout-vars.$header-height;
  width: 100%;
  align-items: center;
  z-index: 1;
  pointer-events: all;
  touch-action: auto;
}

.headerBackground {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
  background: var(--theme-color-background-base-2);
  border-bottom: 1px solid var(--theme-color-dark-2);
  opacity: var(--header-background-opacity);
}

.headerContents {
  flex-direction: row;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 2;

  .title {
    align-items: center;
    // border: 2px solid var(--theme-color-red-9);
    // padding: clamp(2px, 0.5vw, 4px) clamp(16px, 2.5vw, 32px);
    // border-radius: 16px;
    font-family: Syncopate, sans-serif;
    letter-spacing: 6px;

    .logo {
      width: 20px;
      margin-right: 6px;
    }
  }

  .links {
    gap: clamp(24px, 5vw, 64px);
    align-items: center;
    margin-bottom: 4px;

    button {
      padding-bottom: 4px;
      text-transform: uppercase;
      color: var(--theme-color-white);
    }
    
    :global(.font) {
      font-size: clamp(12px, 0.8vw, 16px) !important;
    }
  }
}

.headerAnchor {
  &.active {
    border-bottom: 2px solid var(--theme-color-gray-6);
  }
}

.socials {
  gap: clamp(20px, 1.25vw, 42px);

  svg {
    height: clamp(24px, 1.1vw, 32px);
    width: clamp(24px, 1.1vw, 32px);

    * {
      fill: var(--theme-color-white);
    }
  }
}